.aboutme-body {
        height: 100%;
        width: 100%;
        margin: auto;
        display: table;
        font-family: 'Baron Neue',sans-serif;
        background: #fff;
        color: #0b0b0c;
}

.aboutme-body h1 {
        text-transform: uppercase;
        font-size: 4rem;
        color: #0b0b0c;
        margin: 2rem 0;
        padding: 0 0 2rem 0;
        text-align: center;
        position: relative;
}

.aboutme-body h1:after {
        content: '';
        position: absolute;
        width: 80px;
        height: 5px;
        margin-left: -40px;
        left: 50%;
        bottom: 0px;
        background-color: #00af83;
}

.aboutme-body .col-container {
        display: flex;
        flex-flow: row wrap;
        width: 75%;
        align-content: center;
        justify-content: space-evenly;
        margin: 3rem auto 0 auto;
}

.aboutme-body .col-left, .aboutme-body .col-right {
        width: 50%;
        height: 100%;
        min-width: 400px;
        vertical-align: middle;
}

.profile{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        
}

.profile img {
        overflow: hidden;
        border-radius: 50%;
        width: 250px;
        height: auto;
        margin: auto;
        -webkit-transition: all 0.5s ease-in-out;
        transition: all 0.5s ease-in-out;
}

.profile img:hover {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
}

.aboutme-body p {
        font-family: 'Exo 2', sans-serif;
        font-size: 1.5rem;
        font-weight: 100;
        text-align: center;
        padding: 0 3rem;
}

.aboutme-body p .full-stack {
        display: inline;
        color: #00af83;
}

/* SKILLS SECTION */
.skills {
        list-style: none;
        width: 100%;
}

.skills li {
        margin: 1rem 0;
}

.bar {
        background: #0b0b0c;
        display: block;
        height: 20px;
        border-radius: 3px;
        overflow: hidden;
}

.bar span {
        height: 20px;
        float: left;
        background: #00af83;
}

.html {
        width: 90%;
}

.css {
        width: 80%;
}

.js {
        width: 80%;
}

.react {
        width: 65%;
}

.node {
        width: 50%;
}

.postgres {
        width: 75%;
}

.express {
        width: 50%;
}

